.mobileMenu-wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: $primarycolor;
  .menu-head {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .logo {
      img {
        width: 200px;
        height: 70px;
        cursor: pointer;
      }
    }
    .close {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      img {
        width: 20px;
        height: 20px;
        cursor: pointer;
      }
      img:hover {
        transform: scale(1.1);
      }
    }
  }
  .menu-body {
    display: flex;
    flex-direction: column;
    margin-top: 2rem;
    align-items: center;
    .link {
      margin-bottom: 1rem;
      text-decoration: none;
      font-family: $textfontbold;
      font-weight: 500;
      color: white;
    }
    .link:hover {
      color: $secondarycolor;
    }
    .active {
      background-color: transparent;
    }
  }
  .mobileMenu-icons {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-end;
    height: 200px;
    .social-icon {
      width: 30px;
      height: 30px;
      padding-left: 0.5rem;
      margin-bottom: 0.2rem;
      cursor: pointer;
    }
    .social-icon-1 {
      width: 35px;
      height: 35px;
      margin-right: 0.5rem;
      padding-left: 0.5rem;
      cursor: pointer;
    }
    img:hover {
      transform: scale(1.1);
    }
  }
}
@media only screen and (min-width: 1024px) {
  .mobileMenu-wrapper {
    display: none;
  }
}
